<template>
  <div class="cart_bottom">
    <div class="left" @click="allChecked">
      <div
        class="all_checked"
        :style="{ background: isCheckedAll && storeNum > 0 ? 'url(' + CheckedImg + ') 0% 0% / 100% 100%' :'url(' + noCheckedImg + ') 0% 0% / 100% 100%' }"
        
      ></div>
      <span class="all_txt">全选</span>
    </div>

    <div class="right">
      <div class="total">
        合计：<span class="total_price">{{ totalPrice }}</span>
      </div>
      <span
        class="pay"
        :class="{ disabled: storeNum <= 0 || payNum.length === 0 }"
        @click="buy"
        >结算({{ storeNum > 0 ? payNum.length : 0 }})</span
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      noCheckedImg: require("@/assets/img/common/noChecked.png"),
      CheckedImg: require("@/assets/img/common/checked.png"),
    };
  },
  computed: {
    storeNum() {
      //购物车仓库数量
      return this.$store.state.cartList.length;
    },
    totalPrice() {
      //总价
      return this.$store.state.cartList.length > 0
        ? "￥ " +
            this.$store.state.cartList
              .filter((item) => {
                return item.checked;
              })
              .reduce((preValue, item) => {
                return preValue + parseInt(item.price) * parseInt(item.count);
              }, 0)
              .toFixed(2)
        : "￥ 0";
    },
    payNum() {
      //结算数量
      return this.$store.state.cartList.length > 0
        ? this.$store.state.cartList.filter((item) => {
            return item.checked;
          })
        : "0";
    },
    isCheckedAll(){ //全选按钮状态 有一个没有选中的情况下全选按钮不选中
      return !(this.$store.state.cartList.filter(item => !item.checked).length)

    }
  },
  mounted() {},
  methods: {
    allChecked() {
      if(this.isCheckedAll){ //如果全部选中 则取消全选全部
        for(let i=0; i<this.$store.state.cartList.length; i++){
          this.$store.state.cartList[i].checked = false
        }
      }else{ //否则全选全部
        for(let i=0; i<this.$store.state.cartList.length; i++){
          this.$store.state.cartList[i].checked = true
        }
      }
    },
    buy(){ //结算操作
      if(this.$store.state.cartList.filter(item => !item.checked).length === this.$store.state.cartList.length){ //全部不选中
        this.$toast.showToast('您还没有选择宝贝哦~',1500)
      }else{
        this.$toast.showToast('结算功能没有喔~',1500)
      }
    }
  },
};
</script>

<style scoped>
.cart_bottom {
  line-height: 54px;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 49px;
  border-bottom: 1px solid #f8f8f8;
  /* background: #eee; */
  background: #eee;
  height: 54px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 15px;
  justify-content: space-between;
}
.left {
  display: flex;
}
.all_checked {
  width: 20px;
  height: 20px;
}
.all_txt {
  font-size: 12px;
  line-height: 20px;
  color: #666666;
  margin-left: 5px;
}
/* .right{
  display: flex;
} */
.total {
  font-size: 15px;
  color: #333;
  display: inline-block;
}
.total_price {
  font-size: 15px;
  color: #ff5000;
}
.pay {
  background-image: linear-gradient(90deg, #ff9000 0%, #ff5000 98%);
  font-size: 15px;
  line-height: 15px;
  padding: 8px 30px;
  text-align: center;
  border-radius: 80px;
  color: #fff;
  white-space: nowrap;
  margin-left: 10px;
}
.disabled {
  color: rgba(255, 255, 255, 0.5);
}
</style>