<!--
 * @name: 
 * @test: test font
 * @msg: 
 * @param: 
 * @return: 
-->
<template>
  <div class="cart_goods_list">
    <div class="cart_goods_checked"  :style="{background: cartGoodsList.checked && cartGoodsList.count !== 0 ? 'url('+CheckedImg+') 0% 0% / 100% 100%' : 'url('+noCheckedImg+') 0% 0% / 100% 100%'}" @click="checked">
    </div>

    <div class="cart_goods_img">
      <img
        :src="'http:'+cartGoodsList.imges"
        alt=""
      />
    </div>

    <div class="cart_goods_info">
      <div class="title">
        {{cartGoodsList.title}}
      </div>
      <div class="cart_goods_notice">
        商品描述: {{cartGoodsList.title}}
      </div>

      <div class="buy_info">
        <div class="price">{{cartGoodsList.price}}</div>
        <div class="goods_handle">
          <img src="https://img.alicdn.com/tfs/TB1I05Qe1uSBuNjSsplXXbe8pXa-45-40.png?getAvatar=avatar_50x50q90_.webp" alt="" class="sub" @click="subGoods">
          <input type="text" class="nums" v-model="numsVal">
          <img src="https://img.alicdn.com/tfs/TB12tL6ThTpK1RjSZFKXXa2wXXa-45-40.png?getAvatar=avatar_50x50q90_.webp" alt="" class="add" @click="addGoods">
        </div>
        <!-- <div class="count">X{{cartGoodsList.count}}</div> -->
      </div>
    </div>

    
  </div>


  
</template>

<script>
export default {
  props:{
    cartGoodsList:{
      type: Object,
      default(){
        return {}
      }
    }
  },
  data(){
    return{
      noCheckedImg: require('@/assets/img/common/noChecked.png'),
      CheckedImg: require('@/assets/img/common/checked.png'),
      numsVal: this.cartGoodsList.count
    }
  },
  mounted(){
    // console.log(this.cartGoodsList)
  },
  methods:{
    checked(){
      this.cartGoodsList.checked = !this.cartGoodsList.checked
    },
    subGoods(){ //减少商品数量操作
      if(this.numsVal <= 0 || this.cartGoodsList.count <= 0){
        this.numsVal = 1
        return;
      }else if(this.numsVal === 1 || this.cartGoodsList.count === 1){
        return;
      }else{
        this.cartGoodsList.count -= 1
        this.numsVal = this.cartGoodsList.count
      }
      
      
    },
    addGoods(){ //增加商品数量操作
      this.cartGoodsList.count += 1
      this.numsVal = this.cartGoodsList.count
    }
  }
};
</script>

<style scoped>
.cart_goods_list {
  padding: 10px 0px;
  display: flex;
  box-sizing: border-box;
  border-bottom: 1px solid #ccc;
  align-items: center;
  
}
.cart_goods_checked{
  width: 18px;
  height: 18px;
  margin-right: 5px;
  position: relative;
  left: 2px;
}
.cart_goods_img img {
  width: 80px;
  height: 100px;
  display: block;
  border-radius: 6px;
}
.cart_goods_info {
  width: 200px;
  padding: 0px 10px;
}
.title {
  font-size: 17px;
  color: #333;
}
.title,
.cart_goods_notice {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.cart_goods_notice {
  font-size: 14px;
  margin-top: 15px;
  color: #666;
}
.buy_info{
  display: flex;
  justify-content: space-between;
  margin-top: 25px;
  font-size: 17px;
}
.price{
  color: orangered;
}
.sub,.add{
  width: 25px;
  height: 22px;
}
.nums{
  width: 40px;
  height: 20px;
  border: 1px solid #E8E8E8;
  display: inline-block;
  position: relative;
  top: 0;
  text-align: center;
  color: rgb(102,102,102);
  font-size: 12px;
}
</style>