<template>
  <div class="cart">
    <NavBar class="cart_nav">
      <div slot="nav_center" class="nav_title">购物车({{cartNum}})</div>
    </NavBar>
    <Scroll class="content">
      <div v-if="cartNum !== 0">
        <CartGoodsList v-for="(item,index) in cartGoods" :key="index" :cartGoodsList="item"></CartGoodsList>
      </div>

      <div v-else>
        <CartEmpty></CartEmpty>
      </div>
    </Scroll>
    <CartBottomUntil v-if="cartNum !== 0"></CartBottomUntil>
  </div>
</template>

<script>
import NavBar from "@/components/navBar/common/NavBar";
import Scroll from "@/components/scroll/Scroll";

import CartGoodsList from "./childrenComps/CartGoodsList";
import CartBottomUntil from "./childrenComps/CartBottomUntil";
import CartEmpty from "./childrenComps/CartEmpty";
export default {
  name: "cart",
  components:{
    NavBar,
    Scroll,
    CartGoodsList,
    CartBottomUntil,
    CartEmpty,
  },
  computed:{
    cartNum(){ //购物车总数
      return this.$store.state.cartList.length
    },
    cartGoods(){
      return this.$store.state.cartList
    }
  },
  mounted(){
    console.log(this.$store.state.cartList.length)
  }
}
</script>

<style scoped>
.cart{
  height: 100vh;
}
.content{
  height: calc(100% - 93px);
}
.cart_nav{
  background: pink;
  color: #fff;
}
.nav_title{
  text-align: center;
}
</style>